import * as THREE from 'three'
export default function RotationHelper(rotateOn, camera, glb) {
  if (rotateOn === 'front') {
    console.log('side: ', (1 * Math.PI) / 2)
  } else if (rotateOn === 'back') {
    glb.scene.rotateY(1.5707963267948966) //
    console.log('back rotation: ', glb.scene.rotation)
    console.log('side: ', (1 * Math.PI) / 2)
  } else if (rotateOn === 'top') {
    camera.position.set(0, 1, -0.1)
  } else if (rotateOn === 'bottom') {
    camera.position.set(-0, -1, -0.05)
  } else if (rotateOn === 'left') {
    glb.scene.rotateY(1.5707963267948966)
  } else if (rotateOn === 'right') {
    glb.scene.rotateY(1.5707963267948966)
  }
}
